@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.widget{visibility:visible;opacity:1;transition:all .5s ease-in-out;-webkit-transition:all .5s ease-in-out;-moz-transition:all .5s ease-in-out;-ms-transition:all .5s ease-in-out;-o-transition:all .5s ease-in-out}.hidden{visibility:hidden;opacity:0;pointer-events:none}

.bas-font{font-family:'Bebas Neue', cursive}.text-14{font-size:14px}.text-white{color:white !important}.text-white svg{color:white !important}.text-white:hover{color:#F44336}.dec-none{text-decoration:none}.color-primary{color:#F44336}.text-white{color:white}.gray42{color:#424242}.gray75{color:#757575}.gray9e{color:#9E9E9E}.product-grid{max-width:1200px;margin:10px auto;padding:0 20px;width:auto;display:grid;grid-template-columns:repeat(auto-fill, minmax(300px, 1fr));grid-column-gap:1rem;grid-row-gap:4rem}.nav-link{font-family:'Bebas Neue', cursive;font-size:17px;color:#F44336}.dropnav-link{font-family:'Bebas Neue', cursive;font-size:17px;text-decoration:none}.dropnav-link:hover{color:#424242}.navbar-dark .navbar-nav .nav-link{color:white}.nav-brand{color:#F44336;font-size:22px;text-decoration:none !important}.nav-brand:hover{color:#C62828}.cat-btn{transition:-webkit-transform 150ms ease-in-out;transition:transform 150ms ease-in-out;transition:transform 150ms ease-in-out, -webkit-transform 150ms ease-in-out}.cat-btn:hover{-webkit-filter:brightness(50%);filter:brightness(50%);transform:scale(1.01);-webkit-transform:scale(1.01);-moz-transform:scale(1.01);-ms-transform:scale(1.01);-o-transform:scale(1.01)}.carousel-height{height:50vh}.carousel-label{background-color:#0000004a;margin:0 auto;width:30vw}.carousel-label p{font-size:18px;font-weight:600}.footer-pos{position:absolute;bottom:0}.b-example-divider{height:3rem;background-color:rgba(0,0,0,0.1);border:solid rgba(0,0,0,0.15);border-width:1px 0;box-shadow:inset 0 0.5em 1.5em rgba(0,0,0,0.1),inset 0 0.125em 0.5em rgba(0,0,0,0.15)}.bi{vertical-align:-.125em;fill:currentColor}@media (max-width: 892px){.carousel-height{height:20vh}.carousel-label{width:70vw}}

